  transition: all .2s ease-in-out;
  }
   

  .bm-burger-bars {
    background: #999fb6;
  }

  .bm-cross-button {
    height: 24px;
    width: 24px;
  }
   
  
  .bm-cross {
    background: #bdc3c7;
  }
   
  .bm-menu-wrap {
    position: fixed;
    height: 100%;
  }
   

  .bm-menu {
    background: #b7bfe4;
    padding: 2.5em 1.5em 0;
